@-webkit-keyframes icon--spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 0.5; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    opacity: 0.5; } }

@keyframes icon--spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 0.5; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    opacity: 0.5; } }

/* Applied colors */
.Avatar {
  width: 100%;
  height: 100%; }
  .Avatar__container {
    height: 46px;
    width: 46px;
    border-radius: 100px;
    overflow: hidden;
    border: 3px solid white;
    -webkit-box-shadow: 0 6px 8px rgba(102, 119, 136, 0.12), 0 1px 2px rgba(102, 119, 136, 0.53);
            box-shadow: 0 6px 8px rgba(102, 119, 136, 0.12), 0 1px 2px rgba(102, 119, 136, 0.53); }



