@-webkit-keyframes icon--spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 0.5; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    opacity: 0.5; } }

@keyframes icon--spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 0.5; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    opacity: 0.5; } }

/* Applied colors */
.About {
  margin-bottom: 13em;
  margin-top: 30px;
  font-size: 1.25em;
  max-width: 900px;
  margin: 0 auto; }



