@-webkit-keyframes icon--spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 0.5; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    opacity: 0.5; } }

@keyframes icon--spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 0.5; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    opacity: 0.5; } }

/* Applied colors */
.Chart {
  overflow: visible; }
  .Chart__container {
    position: relative; }
  .Chart circle {
    opacity: 0.75; }
    .Chart circle:hover {
      opacity: 1; }



