@-webkit-keyframes icon--spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 0.5; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    opacity: 0.5; } }

@keyframes icon--spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 0.5; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    opacity: 0.5; } }

/* Applied colors */
.Label {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.03em;
  font-size: 0.75em; }
  .Label--primary {
    color: #000000;
    font-weight: 800; }
  .Label--secondary {
    color: #a5a5a5; }
  .Label--heading {
    border-bottom: 1px solid #e0e0e0;
    padding-bottom: 10px; }
  .Label--table {
    font-size: 0.7em;
    color: #a5a5a5;
    font-weight: 600; }



