@-webkit-keyframes icon--spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 0.5; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    opacity: 0.5; } }

@keyframes icon--spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 0.5; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    opacity: 0.5; } }

/* Applied colors */
.Spinner__container .Spinner {
  width: 32px; }

.Spinner__container--size-xl .Spinner {
  width: 100px; }

.Spinner__container--spin .Spinner {
  -webkit-animation: spin 80s linear infinite;
          animation: spin 80s linear infinite; }

@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(6080deg);
            transform: rotate(6080deg); } }

@keyframes spin {
  to {
    -webkit-transform: rotate(6080deg);
            transform: rotate(6080deg); } }



